import {  Injectable} from "@angular/core";
import { HttpClient,HttpHandler, HttpErrorResponse } from "@angular/common/http";
import { url } from 'src/app/globalmodel/url';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';


@Injectable()
export class AdList{

getAllAdsUrl=url.getAllAds;
    constructor(private http:HttpClient){

    }
    handleError(error:HttpErrorResponse){
       console.log(error);
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } else if(error.status!=200){
         err = error.error;
             }
      
              return throwError(
          err);
             };

             getListAds():Observable<any>{
                return this.http.get(this.getAllAdsUrl,httpOptions.jsonHeaders).pipe(catchError(this.handleError))
             }
            }