import {Subads} from './subads.modal'
import { Params } from './params.modal';
export class Ads{
    private subAds:Subads[];
  public typeOfAd:string;
  private category:string;
  private noOfAds:string;
  private layout:string;
 private priority:number;
  constructor() {
    this.subAds=[];
    this.category='';
    this.noOfAds='';
   }

   set priorityvalue(priority:number){
     this.priority=priority;
   }

set categoryName(category:string){
this.category=category;
}
set noofads(noofads:string){
  this.noOfAds=noofads;
  }

  set layoutvalue(layout:string){
    this.layout=layout;
    }

    set subadsarray(subads:any[]){
      if(subads!=null){
      subads.forEach(subad=>{
        this.subAds.push(subad);
      })
    }else {
      let subad = new Subads();
      subad.labelname='';
      subad.linkurl='';
      subad.imageObj={
        url:null,
        key:null
      };
      subad.statestatus='inactive';
      let array=[];
      let param = new Params();
      param.keywordvalue='';
      param.categoryvalue=this.category;
      param.paramvalue='';
      array.push(param);
      subad.paramaters=array;
      this.subAds.push(subad);
    }
      
    
      }
get priorityvalue(){
  return this.priority;
}

    get categoryName(){
      return this.category;
    }
    get noofads(){
      return this.noOfAds;
    }
    get layoutvalue(){
      return this.layout;
    }
    get subadsarray(){
      return this.subAds;
    }
      

}