    openSnackBar(message: string, action: string) {
      this.snackBar.open(message, action, {
        duration: 5000,
      });
    }
    ngOnInit() {
      this.footerNavService.getData().subscribe(data=>{
        if(data.status=='S'){
          this.aboutDataOfObject=data.data;
          this.initializeArrayOfList();
          this.createForm();
        }else{
          this.initializeArrayOfList();
          this.createForm();
        }
      },err=>{
        this.initializeArrayOfList();
        this.createForm();
      })
     
    }
createForm() {
    this.footerForm=this.fb.group({
      titleFooter:this.fb.array(this.listObjectArr)
    })
}
initTitleObject(object,array) {
  if(object!='') {
  return this.fb.group({
      titleName:object.titleName,
      titleList:this.fb.array(array)
  })
}
  else {
    console.log('i m here')
    return this.fb.group({
      titleName:['',[Validators.required]],
      titleList:this.fb.array(this.returnnewArray())
  })
}
}
initializeArrayOfList() {
  if(this.aboutDataOfObject!=null){
  this.aboutDataOfObject.titleFooter.forEach(dataObject => {
    let j=0;
    let arrays=[];
    if(dataObject!=''){
    dataObject['titleList'].forEach(titledesp=>{
     arrays[j] = this.initTitleList(titledesp);
     j++;
    })
  }
   this.listObjectArr.push(this.initTitleObject(dataObject,arrays));
  })
  }
  console.log(this.listObjectArr);
}
returnnewArray(){
let array=[];
array.push(this.initTitleList(''));
  return array;
}
initTitleList(arrayOfObject) {
  if(arrayOfObject!=''){
  return this.fb.group({
    listName:[arrayOfObject.listName,[Validators.required]],
    listLink:[arrayOfObject.listLink,[Validators.required]]
  })
} 
  else {
    return this.fb.group({
      listName:[null,[Validators.required]],
      listLink:[null,[Validators.required]]
    })
  }
}
deleteTitle(index){
  (<FormArray>(this.footerForm.get('titleFooter'))).removeAt(index);
}

deleteListItem(listObject,index){
  (<FormArray>(listObject.get('titleList'))).removeAt(index);
}
addListItem(listobject){
  (<FormArray>(listobject.get('titleList'))).push(this.initTitleList(''));
}

addList() {
   (<FormArray>(this.footerForm.controls.titleFooter)).push(this.initTitleObject('',''));
}
