import {Injectable} from "@angular/core";

import {url} from '../../../../globalmodel/url'
import {of as ObservableOf, BehaviorSubject} from "rxjs";
import { ImageCrud } from '../../moduleServices/imageCrud';
import { ImageObj } from '../../modulemodels/imageObject';
@Injectable()

export class ImageDatabase{
private isDone:boolean=false;

   private imageChange:BehaviorSubject<ImageObj> = new BehaviorSubject<ImageObj>(null); 
  public deletechange:BehaviorSubject<any>= new BehaviorSubject<any>(null);
    constructor(private imageCrud:ImageCrud){
      
}

 public setimageObj(){

 }
 public getdelimageObj(){
   return this.deletechange;
 }

 public getaddimageObj(){
   
return this.imageChange;
 }


      uploadFile(form:FormData){
       
        this.imageCrud.uploadFile(url.localaddImageuri,form).subscribe(data=>{
    
               if(data!=null){
                this.imageChange.next(data);  
                this.isDone=true;  
               }

            },err=>{
                alert(err);
                  this.isDone=false;
            }
         
         
         )
       
         return this.isDone;
      }

      deleteFile(form:FormData){
        
        this.imageCrud.deleteFile(url.localdeleteImageuri,form).subscribe(data=>{
          if(data.isdelete==true){
              this.deletechange.next(data);
          }
        },err=>{
          alert(err);
          this.isDone=false;
        })
      }
      
     


        }
