import {Component, Input, Output, EventEmitter, OnInit } from '@angular/core'
import {getImagesUrl} from './services/getImagesUrl'
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import{ImagedialogComponent} from './imagedialog/imagedialog.component'
@Component({
selector:'employeeData',
templateUrl:'employeeData.component.html',
styleUrls:['employeeData.component.css'],
providers:[getImagesUrl]
})

export class EmployeeDataComponent implements OnInit{
@Input()user;
@Input()index;
@Output() verification = new EventEmitter<any>();
@Output() unverification = new EventEmitter<any>();
private imagesArray:any;
private keyArray:string[];
private imagesUrl:any={}
constructor(public dialog: MatDialog,private getImagesUrl:getImagesUrl){

}
ngOnInit(){
  
this.imagesArray= this.user.ImageUrls[0];
this.keyArray=Object.keys(this.imagesArray).filter(arrayelement=>arrayelement!='_id');
this.getImageUrls();
}

getImageUrls(){
    let obj={'mobile_no':this.user.mobile_no,'keyArray':this.keyArray};
 this.getImagesUrl.getImagesUrl(obj).subscribe(data=>{
     if(data!=null){
     this.imagesUrl=data.Images;
    
  } },
  err=>{
      console.log(err);
  })
}

verify(user){
    this.user.isVerified=true;
this.verification.emit(user);
}
unverify(user){
    this.user.isVerified=false;
this.unverification.emit(user);
}

openimagedialog(image)
{
    const dialogRef = this.dialog.open(ImagedialogComponent, {
        width: '500px',
        data: image
      });
}
} 