import { Injectable } from "@angular/core";
import {HttpClient,HttpErrorResponse} from "@angular/common/http";
import { Observable,throwError } from 'rxjs';
import {url} from '../../../globalmodel/url'
import { catchError, retry } from 'rxjs/operators';

import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { NgForm } from '@angular/forms';

@Injectable()

export class ImageCrud{


    constructor(private http:HttpClient){

    }

    handleError(error:HttpErrorResponse){
      let  errorMessage = 'Network Error';
      if (error.error instanceof ErrorEvent) {
       
        errorMessage = 'FrontEnd Error';
      } else if(error.status==401){
      
        errorMessage = error.error;
      }else if(error.status==403){
        errorMessage = error.error
      }
            return throwError(
        errorMessage);
           };
    uploadFile(url,details:any):Observable<any>{
       
   return this.http.post<any>(url,details).pipe(
   catchError(this.handleError));    
 };

 deleteFile(url,object:any):Observable<any>{
  
   return this.http.post(url,object,httpOptions.jsonHeaders).pipe(
      catchError(this.handleError));    
 }
    }
     
