import {Injectable} from '@angular/core';
import {HttpClient,HttpErrorResponse} from '@angular/common/http';
import { Observable,throwError } from 'rxjs';
//import { httpOptions } from "../../../../globalmodel/headerDetails";
import { catchError, retry } from 'rxjs/operators';
import { NgForm } from '@angular/forms';
import {url} from '../../../../globalmodel/url'
import { httpOptions } from 'src/app/globalmodel/headerDetails';
@Injectable()

export class FooterNavService{
    private pushUrl=url.setFooterNav;
private getUrl= url.getFooterNav;

    constructor(private http:HttpClient){
    }
    handleError(error:HttpErrorResponse){
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } else {
         err = 'BackendError'
             }
      
              return throwError(
          err);
             };
    uploadForm(data:NgForm):Observable<any>{
        
   return this.http.post<any>(this.pushUrl,data,httpOptions.jsonHeaders).pipe(
   catchError(this.handleError));    
   };

   getData():Observable<any>{
   return this.http.get<any>(this.getUrl).pipe(
        catchError(this.handleError));   
   }
 
}