import {  Injectable} from "@angular/core";
import { HttpClient,HttpHandler, HttpErrorResponse } from "@angular/common/http";
import { url } from 'src/app/globalmodel/url';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';


@Injectable()
export class GetUnverifiedUsers{
url = url.localgetUnverifiedEmployees;
    constructor(private http:HttpClient){

    }
    handleError(error:HttpErrorResponse){
       
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } else {
         err=error.error
             }
      
              return throwError(
          err);
             };

    getUsersData():Observable<any>{
       return this.http.get(this.url,httpOptions.jsonHeaders).pipe(catchError(this.handleError));

        
    }
}