export class Params{
    private keyword:string
    private value:string
   private category:string
    constructor(){
        this.keyword='';
        this.value='';
        this.category=''
    }
set keywordvalue(keyword:string){
    this.keyword=keyword;
}
set paramvalue(value:string){
this.value=value;
}

set categoryvalue(category:string){
    this.category=category;
}

get keywordvalue(){
    return this.keyword;
}
get paramvalue(){
    return this.value;
}
get categoryvalue(){
    return this.category;
}
}