import {  Injectable} from "@angular/core";
import { HttpClient,HttpHandler, HttpErrorResponse } from "@angular/common/http";
import { url } from 'src/app/globalmodel/url';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';


@Injectable()
export class Verification{
verifyUrl = url.localverifyUser;
unverifyUrl = url.localUnverifyUser;
    constructor(private http:HttpClient){

    }
    handleError(error:HttpErrorResponse){
      
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } else {
         err = 'BackendError'
             }
      
              return throwError(
          err);
             };

    verifyUser(object):Observable<any>{
       return this.http.post(this.verifyUrl,object,httpOptions.jsonHeaders).pipe(catchError(this.handleError));

        
    }

    unverifyUser(object):Observable<any>{
      return this.http.post(this.unverifyUrl,object,httpOptions.jsonHeaders).pipe(catchError(this.handleError));

    }
}