import { Component, OnInit } from '@angular/core';
import {getProductArray} from '../../../globalServices/filterProductList'
import {Ads} from '../../../globalmodel/ad/ads.modal'
import {AdList} from './services/adList';
@Component({
  selector: 'app-adspage',
  templateUrl: './adspage.component.html',
  styleUrls: ['./adspage.component.css'],
  providers:[AdList]
})
export class AdspageComponent implements OnInit {
private adArray:Ads[]=[];
private currentopened:number=0;
private productArray=[];
private categoryNameArray=[];
private bankArray=[];
private completeList=[];
  constructor(private getAdList:AdList) { }

  ngOnInit(){
    this.getAdList.getListAds().subscribe(data=>{
      //console.log(data);
      if(data!=null && Array.isArray(data)==true && data.length!=0){
        data.forEach(list => {
          if(list.adalready!=null && list.adalready.length!=0){
            this.adArray= list.adalready;
            this.sendcategoryName(this.adArray);
           }
          else if(list.productAd!=null && list.productAd.length!=0){
          let prodlist = getProductArray.getProducts(list.productAd);
           this.completeList.push({'Product':prodlist});
         //  console.log(prodlist);
         }
         else if(list.bankAd!=null && list.bankAd.length!=0){
          
          this.completeList.push({'Bank':list.bankAd});
          
        }
        });
      }else{
        alert('No Product Data');
      }  
    })

  }


  sendcategoryName(array){
  array.forEach(ad => {
    this.categoryNameArray.push(ad.category);
    
  });
  }

  openStatus(i){
    this.currentopened=i;
   // console.log(this.currentopened);
  }
  returnnewAdd(){
    let ad = new Ads();
   ad.categoryName='';
   ad.layoutvalue='';
   ad.noofads='';
   ad.subadsarray=null;
   return ad;

  }
  addAd(){
    this.adArray.push(this.returnnewAdd());
  }
deleteAd(object){
  for(let ad of this.adArray){
    if(ad.categoryName==object.category){
      this.adArray.splice(object.index,1);
    }
  }
  
}

}
