import { FormControl } from '@angular/forms';

export const advalidations={
   categoryNameArray:[],
  

setcategoryNameArray(categoryArray){
advalidations.categoryNameArray=categoryArray;
        
},
    
  

 categoryValidation(control:FormControl){
     let output=null;
   
          advalidations.categoryNameArray.forEach(categoryName=>{
        if((<string>(control.value)).toLowerCase()==(<string>categoryName).toLowerCase()){
           
            output={"repeat":true};
           return;
        }
        
     }) 
 
     return output;


}



}