import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormBuilder, Validators, FormGroup, NgForm, FormArray } from '@angular/forms';
import {getErrorMessage} from '../../../globalServices/Validation'
import { AdCrud } from './services/adCrud';
import { MatSnackBar } from '@angular/material';
import { advalidations } from './services/norepeatvalidation';
@Component({
  selector: 'adscrud',
  templateUrl: './adscrud.component.html',
  styleUrls: ['./adscrud.component.css'],
  providers:[AdCrud]
})
export class AdscrudComponent implements OnInit {@Input()
 @Input()status;
 @Input()ad;
 @Input()oldcategory;
 @Input()index;
 @Input() optionList=null;
 @Output() ademit = new EventEmitter();
@Input()categoryNameArray:string[];

private labelNameArray:string[];
private minSubad=2;
private productArray=[];
private paramArray=[];
private subCategoryArray=[];
private categoryArray=[];
private typeOfAdShow:boolean=false;
 private crudStatus:string='new';
 minparaerrorMessage:string='';
 minaderrorMessage:string='';
  errorMessage:string='';
  
private dataform:FormGroup;
private errors;

openSnackBar(message: string, action: string) {
  this.snackbar.open(message, action, {
    duration: 2000,
  });
}

  private subads:any[]=[];
 
    constructor(private fb:FormBuilder ,private geterror:getErrorMessage,private adcrud:AdCrud,private snackbar:MatSnackBar) { 
      this.errors=this.geterror.geterror();
      
      
    }

createparameters(keyword,currentparams,category):FormGroup{
  
    return this.fb.group({
 keyword:[keyword,Validators.required],
value:[currentparams,Validators.required],
category:[category]
    })
}

urlcrud(obj){  //emit the changes made from backend image data
  
if(obj.crud=='add'){
  (<FormArray>this.dataform.get('subAds')).controls.forEach(subad=>{

    if((<FormGroup>subad).controls.label.value==obj.label){
      
      (<FormGroup>subad).controls.imageobj.get('url').setValue(obj.url);
      (<FormGroup>subad).controls.imageobj.get('key').setValue(obj.key);
    }
  })
  console.log(this.dataform.get('subAds').value)
}
else if(obj.crud=='delete'){
  (<FormArray>this.dataform.get('subAds')).controls.forEach(subad=>{

    if((<FormGroup>subad).controls.label.value==obj.label){
      (<FormGroup>subad).controls.imageobj.get('url').setValue(null);
      (<FormGroup>subad).controls.imageobj.get('key').setValue(null);
    }
  })
}
}
createSubAds(isNew,currentobj,parameter){
  if(isNew==true){
    return this.fb.group({
      label:['',Validators.required],
      imageobj:this.fb.group({
        url:null,
        key:null
      }),
      link:['',Validators.required],
      params:this.fb.array(parameter),
      status:['inactive',Validators.required],
     priority:['',Validators.required]
    })
  }else 
  {
    return this.fb.group({
      label:[currentobj.label,Validators.required],
      imageobj:this.fb.group({
        url:currentobj.imageobj.url,
        key:currentobj.imageobj.key
      }),
      link:[currentobj.link,Validators.required],
      params:this.fb.array(parameter),
      status:[currentobj.status,Validators.required],
      priority:[currentobj.priority,Validators.required]

    })
  }
}

addParameters(subad,keyword,currentparams,category){

 <FormArray>subad.params.push(this.createparameters(keyword,currentparams,category));
 
}

deleteParameters(subad,index){

  (<FormArray>subad.params).removeAt(index);

}


addNewSubAd(isNew,currentobj){
  
  (<FormArray>this.dataform.get('subAds')).push(this.createSubAds(isNew,currentobj,[]));
  this.isAdCountEqual();
  this.minaderrorMessage='';
 
}
deleteThisIndex(subads,index){
 if(subads.length>=this.minSubad){
   (<FormArray>this.dataform.get('subAds')).removeAt(index);
  }
else if(subads.length==this.minSubad-1){
this.minaderrorMessage='Minimum four Ad Should Be Present';
}
}

createform(){
  if(this.ad!=null){
  this.dataform=this.fb.group({
    typeOfAd:[this.ad.typeOfAd,Validators.required],
    category:[this.ad.category,[Validators.required,advalidations.categoryValidation]],
    noOfAds:[this.ad.noOfAds,Validators.required],
    layout:[this.ad.layout,Validators.required],
    priority:[this.ad.priority,Validators.required],
    subAds:this.fb.array(this.subads)
  })
}
else{
  this.dataform=this.fb.group({
    typeOfAd:['',Validators.required],
    category:['',Validators.required],
    noOfAds:['',Validators.required],
    layout:['',Validators.required],
    priority:['',Validators.required],
    subAds:this.fb.array(this.subads)
  })
}
}


deleteThisAd(category){
  this.adcrud.deleteAd({'crud':'delete','category':category}).subscribe(response=>{
    if(response.isDeleted==true){
      this.openSnackBar('Successfull Deletion','');
      window.location.reload();

    }
  },err=>{
    this.openSnackBar(err,'');
  })

}

setcategoryArray(){
 
  let newarray=this.categoryNameArray.filter(element=>{
   return element!=this.ad.category
   
  })
  advalidations.setcategoryNameArray(newarray);

}

intializeselectoption(array,key,obj){
  this.categoryArray=array[0][key];
  
  if(obj!=null){
 let subarray= this.categoryArray.filter(element=>{
    return element.categoryName==obj.category
    
  })
if(subarray.length!=0){
 this.subCategoryArray= subarray[0].subcategory;
 this.setParamArray(this.subCategoryArray,key,obj);
}}
else {
 this.subCategoryArray= this.categoryArray[0].subcategory;
 this.setParamArray(this.subCategoryArray,key,obj);
}
}

setTypeOfAd(obj){
  
if(obj.typeOfAd==null || obj.typeOfAd==''){
  this.productArray=this.optionList;
  this.intializeselectoption(this.productArray,'Product',null)
}
 else if(obj.typeOfAd=='Product'){
    this.productArray=[];
   this.productArray= this.optionList.filter(element=>{
      return Object.keys(element)[0]=='Product';
    })
   this.intializeselectoption(this.productArray,'Product',obj);
    //this.categoryArray[0].product[0].subcategory[0].products[0].subProducts[0];


  }
  else if(obj.typeOfAd=='Bank'){
    this.productArray=[];
    this.productArray= this.optionList.filter(element=>{
       return Object.keys(element)[0]=='Bank';
     })
    
     this.intializeselectoption(this.productArray,'Bank',obj);
    }
}

setParamArray(array,key,obj){
  if(key=='Product'){
    this.paramArray=[];
   this.paramArray= Object.keys((array[0].products[0].subProducts[0].info).priceAndAmount[0]).filter(element=>{
      return element!='_id';
    });
  this.paramArray.push('label');
    
  }

}

ngOnChanges(changes) {
if(changes.status.currentValue=='opened'){
  this.setcategoryArray();
}
}


ngOnInit() {
 if(this.status=='opened'){
   this.setcategoryArray();
 }
  this.setTypeOfAd(this.ad);

if(this.ad.category==''){
this.crudStatus='new';
}
else {
  this.crudStatus='edit';
  this.oldcategory=this.ad.category;
}

this.initialize();
this.createform();
this.dataform.controls.typeOfAd.valueChanges.subscribe(data=>{
 
 this.setTypeOfAd(this.dataform.getRawValue());

})
this.dataform.controls.noOfAds.valueChanges.subscribe(data=>{
  this.isAdCountEqual();

})


this.dataform.controls.category.valueChanges.subscribe(data=>{
 
  let array=this.productArray[0][Object.keys(this.productArray[0])[0]].filter(element => {  //set the new subcategories of new category
    return element.categoryName==data
  });

  
    this.subCategoryArray=array[0].subcategory;
  (<FormArray>this.dataform.controls.subAds).controls.forEach(subad=>{
   (<FormArray>(<FormGroup>subad).controls.params).controls.forEach(obj=>{
      (<FormGroup>obj).controls.category.setValue(data);
   })
     })
    
})

  }

  initialize(){
if(this.ad!=null){
  let i=0;
  this.ad.subAds.forEach(element=>{
    let j=0;
    let arrays=[];
    element.params.forEach(object=>{ 
       arrays[j]=this.createparameters(object.keyword,object.value,this.ad.category);
      j++;
     })
     
     this.subads.push(this.createSubAds(false,element,arrays));
 i++;
  })
}
  }


  formSubmit(form:NgForm){

    if(form!=null){
      console.log(form);
      this.adcrud.pushadd({'crud':this.crudStatus,'data':form ,'oldref':this.oldcategory}).subscribe(data=>{  
        if(data.isAdded==true){
         this.openSnackBar('Successfull Ad Upload','');
         this.ad.category=form['category'];
         this.ad.layout=form['layout'];
         window.location.reload();

        }
        
      },err=>{
        this.openSnackBar(err,'');
      })

    }
  }

 

  isAdCountEqual(){
  
if(this.dataform.get('noOfAds').value==(<FormArray>this.dataform.get('subAds').value).length){
  this.errorMessage=''
 return true;
}else {
this.errorMessage='No Of Sub ads in option not equal to sub ads present';
return false;

  }
}}

