import {Params} from './params.modal'
export class Subads{
  private label:string;
    private imageobj:any;
  private link:string;
  private params:Params[];
  private status:string;
  private priority:number;
constructor(){
  this.label='';
  this.imageobj={};
  this.link='';
  this.status='';
  this.priority=null;
  this.params=[];
}
set priorityvalue(priority:number){
this.priority=priority;
}

set labelname(label:string){
this.label=label
}
set imageObj(object){
  this.imageobj=object;
}
set linkurl(link:string){
  this.link=link;
}
set statestatus(status:string){
  this.status=status;
}
set paramaters(param:any[]){
  this.params=param;
}
get labelname(){
  return this.label;
  }
  get imageObj(){
    return this.imageobj;
  }
  get linkurl(){
    return this.link
  }
get statestatus(){
    return this.status;
  }
  get paramaters(){
    return this.params
  }
  
  get priorityvalue(){
    return this.priority;
  }


}