import {  Injectable} from "@angular/core";
import { HttpClient,HttpHandler, HttpErrorResponse } from "@angular/common/http";
import { url } from 'src/app/globalmodel/url';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';


@Injectable()
export class AdCrud{
addorEditurl = url.localpushAd;
deleteUrl=url.localadDelete;
getAllAdsUrl=url.getAllAds;
    constructor(private http:HttpClient){

    }
    handleError(error:HttpErrorResponse){
       console.log(error);
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } else if(error.status!=200){
         err = error.error;
             }
      
              return throwError(
          err);
             };

             getListAds(){
                 this.http.get(this.getAllAdsUrl,httpOptions.jsonHeaders).pipe(catchError(this.handleError))
             }

pushadd(object):Observable<any>{
       return this.http.post(this.addorEditurl,object,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    }

    deleteAd(object):Observable<any>{
        return this.http.post(this.deleteUrl,object,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
 
    }
}