import {  Injectable} from "@angular/core";
import { HttpClient,HttpHandler, HttpErrorResponse } from "@angular/common/http";
import { url } from 'src/app/globalmodel/url';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';


@Injectable()
export class getImagesUrl{
url = url.localgetImagesUrl;
    constructor(private http:HttpClient){

    }
    handleError(error:HttpErrorResponse){
       console.log(error);
        let err='';
          if(error.error instanceof ErrorEvent){
           err='FrontEnd Error'
            } else {
         err = 'BackendError'
             }
      
              return throwError(
          err);
             };

    getImagesUrl(object):Observable<any>{
       return this.http.post(this.url,object,httpOptions.jsonHeaders).pipe(catchError(this.handleError));

        
    }
}